let msgType = require("../../../msgtype");
let EMOJI_STATUS = {
	OPENED: "showEmoji",
	CLOSED: "emoji_list",
};
export default {
	components: {
		
	},
	props: {
		
	},
	data() {
		return {
			show: EMOJI_STATUS.CLOSED,
			emoji: this.$im.Emoji,
			emojiObj: this.$im.EmojiObj,
			
			interval: 5000,
			duration: 1000,
			autoplay: false,
			indicatorDots: true,	// 显示面板指示点
		}
	},
	created() {
		
	},
	amount() {
		console.log(1);
	},
	onReady() {
		
	},
	
	methods: {
		openEmoji(){
			this.show = EMOJI_STATUS.OPENED
		},
		
		cancelEmoji(){
			this.show = EMOJI_STATUS.CLOSED
		},
		
		// 输出 emoji
		sendEmoji(event){
			var emoji = event.target.dataset.emoji;
			this.$emit('newEmojiStr', {
				msg: emoji,
				type: msgType.EMOJI,
			})
		},
	}
}
